<template>
	<div class="searchwp-metrics__insight">
		<span :class="'dashicons dashicons-' + icon"></span>
		<div class="searchwp-metrics__engine-suggestions-insight-content">
			<p>There {{ postCount | contextualizeAlpha }} <strong>{{ postCount }}</strong> popular {{ postCount | contextualizeBeta }} that {{ postCount | contextualizeAlpha }} viewed more than the average. Does this content need to be more prominant?&nbsp;
				<button v-if="buttonText" class="searchwp-metrics-nonbutton">{{ buttonText }}</button>
			</p>
		</div>
	</div>
</template>

<script>
import Vue from 'vue';

export default {
	filters: {
		contextualizeAlpha: function (value) {
			if (parseInt(value,10)===1) {
				return 'is';
			} else {
				return 'are';
			}
		},
		contextualizeBeta: function (value) {
			if (parseInt(value,10)===1) {
				return 'entry';
			} else {
				return 'entries';
			}
		},
	},
	props: {
		postCount: Number,
		icon: {
			type: String,
			default: 'awards'
		},
		buttonText: {
			type: String,
			default: 'Details'
		}
	}
}
</script>

